body 
    {
        font-family: sans-serif;
        margin: 20px;
        padding: 0;
        background: linear-gradient(to bottom right, #55566a, #282834);
        display: flex;
        justify-content: center;
        height: 100vh;
    }

h1 
    {
        margin-top: 0;
        font-size: 22px;
    }

h2 
    {
        margin-top: 0;
        font-size: 20px;
    }

    .board-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 20px auto;
      padding: 10px;
      background-color: #f0f0f0; /* Optional: add background color to make the rounded corners more visible */
      border-radius: 15px; /* Adjust the value to your liking */
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Optional: add some shadow for a nicer effect */
  }

.square 
    {
        background: #fff;
        border: 1px solid black;
        float: left;
        font-size: 24px;
        font-weight: bold;
        line-height: 34px;
        height: 50px;
        width: 50px;
        font-size: 50px;
        padding: 0;
        text-align: center;
    }

.board-row:after 
    {
        clear: both;
        content: '';
        display: table;
    }


    /* CSS provided online*/
    .button{
      margin-right: 10px;
      align-items: center;
      appearance: none;
      background-color: #fff;
      border-radius: 24px;
      border-style: none;
      box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px,rgba(0, 0, 0, .14) 0 6px 10px 0,rgba(0, 0, 0, .12) 0 1px 18px 0;
      box-sizing: border-box;
      color: #3c4043;
      cursor: pointer;
      display: inline-flex;
      fill: currentcolor;
      font-family: "Google Sans",Roboto,Arial,sans-serif;
      font-size: 14px;
      font-weight: 500;
      height: 48px;
      justify-content: center;
      letter-spacing: .25px;
      line-height: normal;
      max-width: 100%;
      overflow: visible;
      padding: 10px 25px;
      position: relative;
      text-align: center;
      text-transform: none;
      transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1),opacity 15ms linear 30ms,transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
      user-select: none;
      -webkit-user-select: none;
      touch-action: manipulation;
      width: auto;
      will-change: transform,opacity;
      z-index: 0;
    }
    
    .button:hover {
      background: #F6F9FE;
      color: #174ea6;
    }
    
    .button:active {
      box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%), 0 8px 12px 6px rgb(60 64 67 / 15%);
      outline: none;
    }
    
    .button:focus {
      outline: none;
      border: 2px solid #4285f4;
    }
    
    .button:not(:disabled) {
      box-shadow: rgba(60, 64, 67, .3) 0 1px 3px 0, rgba(60, 64, 67, .15) 0 4px 8px 3px;
    }
    
    .button:not(:disabled):hover {
      box-shadow: rgba(60, 64, 67, .3) 0 2px 3px 0, rgba(60, 64, 67, .15) 0 6px 10px 4px;
    }
    
    .button:not(:disabled):focus {
      box-shadow: rgba(60, 64, 67, .3) 0 1px 3px 0, rgba(60, 64, 67, .15) 0 4px 8px 3px;
    }
    
    .button:not(:disabled):active {
      box-shadow: rgba(60, 64, 67, .3) 0 4px 4px 0, rgba(60, 64, 67, .15) 0 8px 12px 6px;
    }
    
    .button:disabled {
      box-shadow: rgba(60, 64, 67, .3) 0 1px 3px 0, rgba(60, 64, 67, .15) 0 4px 8px 3px;
    }


    .navbar {
      background-color: #333;
      overflow: hidden;
  }
  
  .navbar a {
      float: left;
      display: block;
      color: white;
      text-align: center;
      padding: 14px 20px;
      text-decoration: none;
  }
  
  .navbar a:hover {
      background-color: #ddd;
      color: black;
  }